<template>
    <div class="content overflow-hidden">
        <div class="row">
            <div class="col-sm-8 col-sm-offset-2 col-md-6 col-md-offset-3 col-lg-4 col-lg-offset-4">
                <!-- Login Block -->
                <div class="block block-themed animated fadeIn">
                    <div class="block-header bg-primary">
                        <ul class="block-options">
                            <li>
                                <a @click="showModal = true">忘记密码?</a>
                            </li>
                        </ul>
                        <h3 class="block-title">登录</h3>
                    </div>
                    <div class="block-content block-content-full block-content-narrow">
                        <!-- Login Title -->
                        <h1 class="h2 font-w600 push-30-t push-5">问答社区管理后台</h1>
                        <p>欢迎, 请登录.</p>
                        <!-- END Login Title -->

                        <!-- Login Form -->
                        <!-- jQuery Validation (.js-validation-login class is initialized in js/pages/base_pages_login.js) -->
                        <!-- For more examples you can check out https://github.com/jzaefferer/jquery-validation -->
                        <form class="js-validation-login form-horizontal push-30-t push-50" method="post">
                            <div class="form-group">
                                <div class="col-xs-12">
                                    <div class="form-material form-material-primary ">
                                        <input class="form-control" type="text" id="login-username" v-model="username"
                                               name="login-username">
                                        <label for="login-username">用户名</label>
                                    </div>
                                </div>
                            </div>
                            <div class="form-group">
                                <div class="col-xs-12">
                                    <div class="form-material form-material-primary ">
                                        <input class="form-control" type="password" id="login-password"
                                               v-model="password" name="login-password">
                                        <label for="login-password">密码</label>
                                    </div>
                                </div>
                            </div>
                            <div class="form-group">
                                <div class="col-xs-12 col-sm-6 col-md-4">
                                    <a class="btn btn-block btn-primary" @click="login"><i
                                            class="si si-login pull-right"></i> 登录</a>
                                </div>
                            </div>
                        </form>
                        <!-- END Login Form -->
                    </div>
                </div>
                <!-- END Login Block -->
            </div>
        </div>
        <!--<confirm title="提示！" content="请联系管理员" :show="showModal" @confirm="showModal=false" @show="showModal=true"-->
                 <!--@hide="showModal=false"></confirm>-->
    </div>
</template>

<script>
    import adminApi from '@/services/admin.js'
    //import store from '@/store/index.js'
//    import Confirm from "@/components/confirm";

    export default {
//        components: {
//            Confirm
//        },
        mounted() {
        },
        data() {
            return {
                username: '',
                password: '',
                showModal: false
            };
        },
        methods: {
            login() {
                let vm = this;
                adminApi.login(this.username, this.password).then(data => {
                    this.$router.push({path: '/topicList'});
                }).catch(err => {
                    console.log(err)
                })
            }
        },
    };
</script>

<style lang="scss">

</style>



